<template>
<span class="task">
  <input type="checkbox" v-bind="task.isDone" @input="$emit('change-done-status')" /> 
  <div
      v-bind:class="[
        { overdue: task.isOverdue && !task.isDone, done: task.isDone },
        'task__content',
      ]"
    >
        <p class="task_name">{{task.taskName}}</p>
        <p class="task_date">{{formatDate(task.dueDate)}}</p>
      </div>
</span>
</template>

<script>
export default {
 name: "Task",
 props: { task: Object },
 methods:{
 formatDate(date) {
  let months = [
        "Jan",
        "Feb",
        "Mar",
        "Apr",
        "May",
        "June",
        "July",
        "Aug",
        "Sept",
        "Oct",
        "Nov",
        "Dec"
      ];
      console.log(date)
      let day = date.split("-")[2];
      let monthNum = date.split("-")[1];
      let month = months[monthNum - 1];

      return `${day} ${month}`;
    },
  },
};
</script>

<style scoped>
.task {
  display: flex;
  align-items: center;
  border: 1px solid #ddd;
  margin-bottom: 15px;
}

.task input[type="checkbox"] {
  margin: 0 20px 0 10px;
}

.task__content {
  display: flex;
  width: 80%;
  justify-content: space-between;
}

.overdue {
  color: red;
}

.done {
  text-decoration: line-through;
}
</style>