<template>
<div class="container">
  <h1>Todo app</h1>
  <section class="tasks">
    <ul>
      <li v-for="task in todoList" :key="task.taskName + task.dueDate">
        <Task :task="task" @change-done-status="task.isDone = !task.isDone"/>
      </li>
    </ul>
  </section>
  <NewTask @addTaskToList = "addTaskToList($event)" />
</div>
</template>

<script>
import Task from "./components/Task"
import NewTask from "./components/NewTask";
export default {
  name: 'App',
  components: {Task, NewTask},
  data: function() {
    return {
      todoList: [
        {
          taskName: "Mop floors",
          dueDate: "2020-11-09",
          isDone: false,
          isOverdue: true,
        },
        {
          taskName: "Meeting",
          dueDate: "2020-11-20",
          isDone: false,
          isOverdue: false,
        },
      ],
    };
  },
  methods: {
    addTaskToList(newTaskInfo) {
      let newTaskObj = {};
      newTaskObj.taskName = newTaskInfo.taskName;
      newTaskObj.dueDate = newTaskInfo.dueDate;
      newTaskObj.isDone = false;
      newTaskObj.isOverdue =
        new Date(newTaskInfo.dueDate) < new Date() ? true : false;
      this.todoList.push(newTaskObj);
      this.sortByDate();
    },
    sortByDate() {
      this.todoList.sort((a, b) => new Date(a.dueDate) - new Date(b.dueDate));
    },
  },
};
</script>

<style scoped>
* {
  box-sizing: border-box;
}

h1 {
  text-align: center;
}

ul {
  padding-left: 0;
  width: 50%;
  margin: 0 auto;
  list-style: none;
}
</style>