<template>
  <div class="new-task">
    <input type="text" 
    placeholder="New task" 
    v-model="newTask" />
    <input type="date" 
    v-model="newTaskDate" />
    <input type="submit" 
    value="Add new task" 
    @click="sendTaskToParent()" />
  </div>
</template>

<script>
export default {
 name: "NewTask",
 data: function() {
     return {
         newTask:"",
         newTaskDate:"",
    };
 },
 methods: {
     sendTaskToParent(){
         let newTaskData = {};
         newTaskData.taskName = this.newTask;
         newTaskData.dueDate = this.newTaskDate;
         this.$emit("add-task-to-list", newTaskData);
         this.newTask ="";
         this.newTaskDate ="";
        },
    },
};
</script>

